import { Button, Dialog, DialogTitle, Stack, TextField } from '@mui/material';
import { useState } from 'react';
import styles from './index.module.scss';

const AddProduct: React.FC<{
  open: boolean;
  handleClose: any;
  feeds?: boolean;
}> = ({ open, handleClose, feeds }) => {
  const [name, setname] = useState('');
  const [image, setimage] = useState(null);
  const [imageDetails, setimageDetails] = useState({ name: '', type: '' });
  const fileUpload = (e: any) => {
    console.log(e);
    let file = e.target.files[0];
    if (file) {
      setimageDetails({ name: file.name, type: file.type });
      const reader = new FileReader();
      reader.onload = (readerEvent: any) => {
        let binaryString: any = readerEvent.target.result;
        // @ts-ignore
        setimage('data:' + file.type + ';base64,' + btoa(binaryString));
      };
      reader.readAsBinaryString(file);
    }
  };
  return (
    <>
      <Dialog onClose={() => handleClose(null, null, true)} open={open}>
        <DialogTitle>{feeds ? 'Add Feed' : 'Add Product'}</DialogTitle>
        <div className={styles.dialogCont}>
          <Stack spacing={3}>
            <TextField
              multiline={feeds}
              rows={feeds ? 4 : 1}
              onChange={(e: any) => setname(e.target.value)}
              value={name}
              label={feeds ? 'Feed Content' : 'Product Name'}
              variant='outlined'
            />
            {image && <img width={50} height={50} src={image} />}
            <Button variant='contained' component='label'>
              Upload Image
              <input
                onChange={fileUpload}
                accept='image/*'
                type='file'
                hidden
              />
            </Button>

            <Button
              onClick={() => handleClose(name, image)}
              variant='contained'
              color='success'
              component='label'>
              Save
            </Button>
          </Stack>
        </div>
      </Dialog>
    </>
  );
};

export default AddProduct;
