// const { getCategoryDetails } = require('../saga/category.saga');
import { keys } from '../keys';
const {
  GET_PRODUCTS,
  FETCH_PRODUCTS,
  UPDATE_SEARCH,
  UPDATE_SORTBY,
  UPDATE_SEL_CATS,
  DELETE_SEL_CATS,
  GET_PRICE_RANGE,
  UPDATE_PRICE_RANGE,
  CREATE_PRODUCT,
  UPDATE_PRICE,
  DELETE_PROD,
} = keys;
export const getProductDetails = () => ({
  type: GET_PRODUCTS,
});

export const fetchProductSuccess = (data: any) => ({
  type: FETCH_PRODUCTS,
  payload: data,
});

export const updateSearch = (data: any) => ({
  type: UPDATE_SEARCH,
  payload: data,
});

export const updateSort = (data: any) => ({
  type: UPDATE_SORTBY,
  payload: data,
});
export const createProduct = (data: any) => ({
  type: CREATE_PRODUCT,
  payload: data,
});
export const updatePrice = (data: any) => ({
  type: UPDATE_PRICE,
  payload: data,
});

export const deleteProds = (data: any) => ({
  type: DELETE_PROD,
  payload: data,
});
