// const { getCategoryDetails } = require('../saga/category.saga');
import { keys } from '../keys';
const {
  REGISTER_USER,
  REGISTER_SUCC,
  REGISTER_ERR,
  VALIDATE_USER,
  FORGOT_SEND_CODE,
  FORGOT_SEND_SUCC,
  CHANGE_PASSWORD,
  RESET_LOGIN,
} = keys;
export const registerUser = (data: any) => ({
  type: REGISTER_USER,
  payload: data,
});

export const regSuccess = (data: any) => ({
  type: REGISTER_SUCC,
  payload: data,
});

export const regFailed = (data: any) => ({
  type: REGISTER_ERR,
  payload: data,
});

export const validateUser = (data: any) => ({
  type: VALIDATE_USER,
  payload: data,
});
export const forgotPassword = (data: any) => ({
  type: FORGOT_SEND_CODE,
  payload: data,
});

export const forgotPasswordSucc = (data: any) => ({
  type: FORGOT_SEND_SUCC,
  payload: data,
});

export const changePassword = (data: any) => ({
  type: CHANGE_PASSWORD,
  payload: data,
});

export const resetLogin = () => ({
  type: RESET_LOGIN,
});
