import Axios from './AxiosHelper';

export default {
  getProducts: (data: any) => {
    return Axios.get('/allProds', data).then((data) => data.data);
  },
  createProducts: (data: any) => {
    return Axios.post('/createProduct', data).then((data) => data.data);
  },
  deleteProducts: (data: any) => {
    return Axios.delete('/deleteProducts', {
      params: { names: JSON.stringify(data) },
    }).then((data) => data.data);
  },
  updatePrice: (data: any) => {
    return Axios.post('/updatePrice', data).then((data) => data.data);
  },
};
