import React, { useEffect } from 'react';
import FeedsScreen from './Feeds';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as Actions from '../../redux/actions/Feed.action';

const Feeds: React.FC = (props: any) => {
  useEffect(() => {
    props.actions.getFeedDetails();
    return () => {};
  }, []);

  return <FeedsScreen feeds={props.data} loading={props.loading} />;
};

const mapDispatchtoProps = (dispatch: any) => ({
  actions: bindActionCreators({ ...Actions }, dispatch),
});

const mapStateToProps = (state: any) => ({
  data: state.Feeds.data,
  loading: state.Feeds.loading,
  error: state.Feeds.error,
});

export default connect(mapStateToProps, mapDispatchtoProps)(Feeds);
