import { all, fork } from 'redux-saga/effects';
import { createFeed, fetchFeeds } from './feeds.saga';
import {
  registerUser,
  validateUser,
  forgotPassword,
  changePassword,
} from './login.saga';
import {
  createProduct,
  deleteProds,
  fetchProducts,
  updatePrice,
} from './products.saga';
import { fetchGraphData, fetchNamesAll } from './trends.saga';

export default function* root() {
  yield all([
    fork(registerUser),
    fork(validateUser),
    fork(forgotPassword),
    fork(changePassword),
    fork(fetchProducts),
    fork(fetchFeeds),
    fork(fetchNamesAll),
    fork(fetchGraphData),
    fork(createProduct),
    fork(updatePrice),
    fork(deleteProds),
    fork(createFeed),
  ]);
}
