import React from 'react';
import './App.css';
import paths from './Routes';

import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import BottomNav from './components/BottomNav';
import Home from './assets/icons/Home.svg';
import Feed from './assets/icons/Feeds.svg';
import Trend from './assets/icons/Trends.svg';

const bottomNav = [
  {
    name: 'Home',
    icon: Home,
    path: '/Home',
  },
  {
    name: 'Feeds',
    icon: Feed,
    path: '/Feeds',
  },
  {
    name: 'Trends',
    icon: Trend,
    path: '/Trends',
  },
];
const ignoreBottom = ['/Login', '/Profile', '/SuperSecretPathVickyNandha'];
const App: React.FC = (): JSX.Element => {
  return (
    <>
      <Router>
        <Routes>
          {paths
            ? paths.map((o) => (
                <Route key={o.path} path={o.path} element={o.component} />
              ))
            : null}
        </Routes>
        <BottomNav details={bottomNav} hideNav={ignoreBottom}></BottomNav>
      </Router>
    </>
  );
};

export default App;
