import { keys } from '../keys';
const {
  GET_NAMES,
  FETCH_NAMES,
  GET_TRENDS_GRAPH_DATA,
  FETCH_TRENDS_GRAPH_DATA,
} = keys;
export const initialState = {
  searchValue: [],
  sortValue: { name: 'Name', asc: true },
  data: [],
  graphData: [],

  loading: false,
  error: null,
};

const TrendsReducer = (state = initialState, action: any) => {
  switch (action.type) {
    case GET_NAMES:
      return {
        ...state,
        loading: true,
      };
    case FETCH_NAMES:
      return {
        ...state,
        data: action.payload,
        loading: false,
        error: null,
      };
    case GET_TRENDS_GRAPH_DATA:
      return {
        ...state,
        loading: true,
      };
    case FETCH_TRENDS_GRAPH_DATA:
      return {
        ...state,
        graphData: action.payload,
        loading: false,
        error: null,
      };
    default:
      return state;
  }
};

export default TrendsReducer;
