import React, { useEffect, useState } from 'react';
import Card from '../../components/card';
import Heading from '../../components/heading';
import Input from '../../components/input';
import styles from './Admin.module.scss';
import Coconut from '../../assets/images/coconut.svg';
import BackTree from '../../components/backgroundTrees';
import Add from '../../assets/icons/Add.svg';
import Delete from '../../assets/icons/delete.svg';
import Profile from '../../components/Profile';
import NewsCard from '../../components/NewsCard';
import { useTheme } from '@emotion/react';

import OutlinedInput from '@mui/material/OutlinedInput';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import GraphTrends from '../../components/GraphTrends';
import AddProduct from '../../components/AddProduct';
import { Button, Stack, TextField } from '@mui/material';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

function getStyles(name: any, personName: any, theme: any) {
  return {
    fontWeight:
      personName.indexOf(name) === -1
        ? theme.typography.fontWeightRegular
        : theme.typography.fontWeightMedium,
  };
}

const AdminScreen: React.FC<{
  loading: any;
  names: any;
  graphData: any;
  graphDataCollector: any;
  createProd: any;
  updatePrice: any;
  deleteProds: any;
  createFeed: any;
}> = ({
  loading,
  names,
  graphData,
  graphDataCollector,
  createProd,
  updatePrice,
  deleteProds,
  createFeed,
}) => {
  const theme = useTheme();
  const [personName, setPersonName] = useState([]);
  const [openAddProd, setopenAddProd] = useState(false);
  const [openFeedAdd, setopenFeedAdd] = useState(false);

  const [price, setprice] = useState(0);
  // console.log(loading, graphData);
  const handleChange = (event: any) => {
    const {
      target: { value },
    } = event;
    setPersonName(
      // On autofill we get a stringified value.
      typeof value === 'string' ? value.split(',') : value
    );
    const values = typeof value === 'string' ? value.split(',') : value;
    // if (values.length > 0) {
    // graphDataCollector(values);
    // }
  };

  const saveProdandClose = (
    name: string,
    image: string,
    onlyClose: boolean
  ) => {
    setopenAddProd(false);
    if (onlyClose) return;
    createProd({ name: name, image: image, priceDetails: [] });
  };
  const saveFeedandClose = (
    name: string,
    image: string,
    onlyClose: boolean
  ) => {
    setopenFeedAdd(false);
    if (onlyClose) return;
    createFeed({ content: name, image: image });
  };

  return (
    <div>
      <Heading>Admin</Heading>
      {/* <Profile /> */}
      <div className={styles.feedAddCont}>
        <Button
          onClick={() => setopenFeedAdd(true)}
          color='primary'
          variant='contained'>
          Add Feed
        </Button>
      </div>
      <div className={styles.SearchCont}>
        {/* <Input icon={Add} placeholder='Add Product' /> */}
        <FormControl sx={{ m: 1, width: 300, mt: 3 }}>
          <Select
            style={{ color: '#587850', backgroundColor: '#fff' }}
            multiple
            displayEmpty
            value={personName}
            onChange={handleChange}
            input={<OutlinedInput />}
            renderValue={(selected) => {
              if (selected.length === 0) {
                return <em>Select Products</em>;
              }

              return selected.join(', ');
            }}
            MenuProps={MenuProps}
            inputProps={{ 'aria-label': 'Without label' }}>
            {/* <MenuItem disabled value=''>
              <em>Select Products</em>
            </MenuItem> */}
            {names.map((name: any) => (
              <MenuItem
                key={name.name}
                value={name.name}
                // style={getStyles(name, personName, theme)}
              >
                {name.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        <div onClick={() => setopenAddProd(true)} className={styles.addProd}>
          <img src={Add} />
        </div>
        {/* <div
          onClick={() =>
            personName.length > 0 ? deleteProds(personName) : null
          }
          className={styles.addProd + ' ' + styles.deleteProd}>
          <img src={Delete} />
        </div> */}
      </div>
      <AddProduct open={openAddProd} handleClose={saveProdandClose} />
      <AddProduct
        feeds={true}
        open={openFeedAdd}
        handleClose={saveFeedandClose}
      />
      {/* <div className={styles.graphContainer}>
        {graphData.length > 0 ? (
          <GraphTrends loading={loading} data={graphData} />
        ) : (
          <span>No Products Selected. Select Product to Display Graph </span>
        )}
      </div> */}
      {personName.length > 0 && (
        <div>
          <Stack style={{ padding: '10px' }} spacing={3}>
            <TextField
              value={price}
              onChange={(e: any) => setprice(e.target.value)}
              type={'number'}
              style={{ color: '#587850', backgroundColor: '#fff' }}
              label='Enter Price for today'
              variant='outlined'
            />
            <Button
              onClick={() =>
                updatePrice({
                  names: personName,
                  priceDetails: { price: +price, date: new Date() },
                })
              }
              variant='contained'>
              Update Price
            </Button>
            <Button
              onClick={() =>
                personName.length > 0 ? deleteProds(personName) : null
              }
              color='error'
              variant='contained'>
              Delete Products
            </Button>
          </Stack>
        </div>
      )}
      <BackTree></BackTree>
    </div>
  );
};

export default AdminScreen;
