import React, { useEffect } from 'react';
import TrendsScreen from './Trends';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as Actions from '../../redux/actions/Trends.action';

const Trends: React.FC = (props: any) => {
  useEffect(() => {
    props.actions.getNamesDetails();
    // props.actions.getGraphDataDetails([]);
    return () => {};
  }, []);

  return (
    <TrendsScreen
      names={props.data}
      loading={props.loading}
      graphData={props.graphData}
      graphDataCollector={props.actions.getGraphDataDetails}
    />
  );
};

const mapDispatchtoProps = (dispatch: any) => ({
  actions: bindActionCreators({ ...Actions }, dispatch),
});

const mapStateToProps = (state: any) => ({
  data: state.Trends.data,
  graphData: state.Trends.graphData,
  loading: state.Trends.loading,
  error: state.Trends.error,
});

export default connect(mapStateToProps, mapDispatchtoProps)(Trends);
