import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  TimeScale,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import colorArray from '../../util/Color';
import 'chartjs-adapter-moment';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  TimeScale,
  Title,
  Tooltip,
  Legend
);

export const options = {
  responsive: true,
  maintainAspectRatio: false,
  tooltips: {
    enabled: true,
  },
  scales: {
    x: {
      type: 'time' as const,
      time: {
        unit: 'day',
        // displayFormats: {
        //   millisecond: 'DD/MM',
        //   second: 'DD/MM',
        //   minute: 'DD/MM',
        //   hour: 'DD/MM',
        //   day: 'DD/MM',
        //   week: 'DD/MM',
        //   month: 'DD/MM',
        //   quarter: 'DD/MM',
        //   year: 'DD/MM',
        // },
      },
    },
  },
  // scales: {
  //   x: {
  //     ticks: {
  //       // Include a dollar sign in the ticks
  //       callback: function (value: any, index: any, ticks: any) {
  //         return '$' + ticks;
  //       },
  //     },
  //   },
  // },
  plugins: {
    legend: {
      position: 'bottom' as const,
    },
  },
};

const formatData = (data: any) => {
  let datasets: any = [];
  data.map((o: any, index: number) => {
    datasets.push({
      label: o.name,
      data: o.priceDetails.map((p: any) => {
        const d = new Date(p.date);
        // d.setHours(0, 0, 0, 0);
        return { x: d, y: p.price };
      }),
      borderColor: colorArray[index],
      backgroundColor: colorArray[index] + '88',
    });
  });

  return { datasets };
};

const GraphTrends: React.FC<{ loading: any; data: any }> = ({
  loading,
  data,
}) => {
  const graphData = formatData(data);
  console.log(graphData);
  // @ts-ignore
  return (
    <Line
      // @ts-ignore
      options={options}
      data={graphData}
      style={{ height: 'calc(100vh - 15rem)' }}
    />
  );
};

export default GraphTrends;
