import Axios from './AxiosHelper';

export default {
  getNames: (data: any) => {
    return Axios.get('/getNames', data).then((data) => data.data);
  },
  getGraphData: (data: any) => {
    // @ts-ignore
    return Axios.get('/getGraphData', {
      params: { names: JSON.stringify(data) },
    }).then((data) => data.data);
  },
};
