// const { getCategoryDetails } = require('../saga/category.saga');
import { keys } from '../keys';
const { GET_FEEDS, FETCH_FEEDS, CREATE_FEEDS } = keys;
export const getFeedDetails = () => ({
  type: GET_FEEDS,
});

export const fetchFeedSuccess = (data: any) => ({
  type: FETCH_FEEDS,
  payload: data,
});

export const createFeed = (data: any) => ({
  type: CREATE_FEEDS,
  payload: data,
});
