import { takeLatest, call, put } from 'redux-saga/effects';
import * as ProductActions from '../actions/Product.action';
import * as TrendsActions from '../actions/Trends.action';
import { Products } from '../../services';
import { keys } from '../keys';

const { GET_PRODUCTS, CREATE_PRODUCT, UPDATE_PRICE, DELETE_PROD } = keys;

export function* fetchProducts() {
  yield takeLatest(GET_PRODUCTS, getCategoryDetails);
}

export function* getCategoryDetails(action: any) {
  // @ts-ignore
  const data: any = yield call(Products.getProducts);
  yield put(ProductActions.fetchProductSuccess(data));
}

export function* createProduct() {
  yield takeLatest(CREATE_PRODUCT, sendProductDetails);
}

export function* sendProductDetails(action: any) {
  // @ts-ignore
  const data: any = yield call(Products.createProducts, action.payload);
}

export function* updatePrice() {
  yield takeLatest(UPDATE_PRICE, sendUpdatedPrice);
}

export function* sendUpdatedPrice(action: any) {
  // @ts-ignore
  const data: any = yield call(Products.updatePrice, action.payload);
  // yield put(ProductActions.fetchProductSuccess(data));
  TrendsActions.getNamesDetails();
}

export function* deleteProds() {
  yield takeLatest(DELETE_PROD, sendDeleteProds);
}

export function* sendDeleteProds(action: any) {
  // @ts-ignore
  const data: any = yield call(Products.deleteProducts, action.payload);
  // yield put(ProductActions.fetchProductSuccess(data));
  TrendsActions.getNamesDetails();
}
