// const { getCategoryDetails } = require('../saga/category.saga');
import { keys } from '../keys';
const {
  GET_NAMES,
  FETCH_NAMES,
  GET_TRENDS_GRAPH_DATA,
  FETCH_TRENDS_GRAPH_DATA,
} = keys;
export const getNamesDetails = () => ({
  type: GET_NAMES,
});

export const fetchNamesSuccess = (data: any) => ({
  type: FETCH_NAMES,
  payload: data,
});

export const getGraphDataDetails = (data: any) => ({
  type: GET_TRENDS_GRAPH_DATA,
  payload: data,
});

export const fetchGraphDataDetailsSuccess = (data: any) => ({
  type: FETCH_TRENDS_GRAPH_DATA,
  payload: data,
});
