import React from 'react';
import styles from './index.module.scss';
interface inputProps {
  value?: string;
  placeholder: string;
  icon?: any;
  type?: string;
  liveValue?: any;
}

const Input = ({ value, placeholder, icon, type, liveValue }: inputProps) => {
  return (
    <>
      <div className={styles.container}>
        {icon ? <img src={icon} alt='' /> : null}
        <input
          value={value}
          // onChange={(e: any) => (liveValue ? liveValue(e.target.value) : '')}
          onKeyUp={(e: any) => (liveValue ? liveValue(e.target.value) : '')}
          placeholder={placeholder}
          className={styles.inputtransparent}
          type={type}
        />
      </div>
    </>
  );
};

export default Input;
