import { takeLatest, call, put } from 'redux-saga/effects';
import * as TrendsActions from '../actions/Trends.action';
import { Trends } from '../../services';
import { keys } from '../keys';

const { GET_NAMES, GET_TRENDS_GRAPH_DATA } = keys;

export function* fetchNamesAll() {
  yield takeLatest(GET_NAMES, getNamesDetails);
}

export function* getNamesDetails(action: any) {
  // @ts-ignore
  const data: any = yield call(Trends.getNames);
  yield put(TrendsActions.fetchNamesSuccess(data));
}

export function* fetchGraphData() {
  yield takeLatest(GET_TRENDS_GRAPH_DATA, getGraphDetails);
}

export function* getGraphDetails(action: any) {
  // @ts-ignore
  const data: any = yield call(Trends.getGraphData, action.payload);
  yield put(TrendsActions.fetchGraphDataDetailsSuccess(data));
}
