import React, { useEffect } from 'react';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as Actions from '../../redux/actions/Trends.action';
import * as Products from '../../redux/actions/Product.action';
import * as Feeds from '../../redux/actions/Feed.action';
import AdminScreen from './Admin';

const Admin: React.FC = (props: any) => {
  useEffect(() => {
    props.actions.getNamesDetails();
    // props.actions.getGraphDataDetails([]);
    return () => {};
  }, []);

  return (
    <AdminScreen
      names={props.data}
      loading={props.loading}
      graphData={props.graphData}
      graphDataCollector={props.actions.getGraphDataDetails}
      createProd={props.actions.createProduct}
      updatePrice={props.actions.updatePrice}
      deleteProds={props.actions.deleteProds}
      createFeed={props.actions.createFeed}
    />
  );
};

const mapDispatchtoProps = (dispatch: any) => ({
  actions: bindActionCreators({ ...Actions, ...Products, ...Feeds }, dispatch),
});

const mapStateToProps = (state: any) => ({
  data: state.Trends.data,
  graphData: state.Trends.graphData,
  loading: state.Trends.loading,
  error: state.Trends.error,
});

export default connect(mapStateToProps, mapDispatchtoProps)(Admin);
