import { keys } from '../keys';
const { GET_FEEDS, FETCH_FEEDS, CREATE_FEEDS } = keys;
export const initialState = {
  searchValue: '',
  sortValue: { name: 'Name', asc: true },
  data: [],

  loading: false,
  error: null,
};

const FeedReducer = (state = initialState, action: any) => {
  switch (action.type) {
    case GET_FEEDS:
      return {
        ...state,
        loading: true,
      };
    case FETCH_FEEDS:
      return {
        ...state,
        data: action.payload,
        loading: false,
        error: null,
      };
    default:
      return state;
  }
};

export default FeedReducer;
