import React, { useEffect, useRef, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import styles from './index.module.scss';

interface params {
  name: string;
  icon: any;
  path: string;
}
interface bottomProps {
  details: Array<params>;
  hideNav?: Array<string>;
}

const BottomNav: React.FC<bottomProps> = ({
  details,
  hideNav = [],
}: bottomProps) => {
  const navigate = useNavigate();
  const location = useLocation();
  const highLight = useRef(null);
  const [leftValue, setleftValue] = useState('0px');
  const [locationchange, setlocationchange] = useState('');
  const onChange = (index: number, path: string) => {
    // debugger;
    // @ts-ignore
    // highLight.current.style.left = index * highLight.current.offsetWidth;
    setleftValue(index * highLight.current.offsetWidth);
    navigate(path);
    // console.log(location);
  };
  useEffect(() => {
    console.log(locationchange);
    setlocationchange(window.location.pathname);
    return () => {
      setlocationchange('');
    };
  }, [location]);
  return (
    <>
      {hideNav.includes(locationchange) ? null : (
        <div className={styles.bottomNavContainer}>
          {details.map((o, i) => (
            <div
              key={'nav_' + i}
              onClick={() => onChange(i, o.path)}
              className={styles.itemHolder}>
              <img alt='' src={o.icon} />
              <span>{o.name}</span>
            </div>
          ))}
          <div
            ref={highLight}
            className={styles.highlighter}
            style={{
              width: 'calc(100vw / ' + details.length + ')',
              left: leftValue,
            }}></div>
        </div>
      )}
    </>
  );
};

export default BottomNav;
