import React, { useState } from 'react';
import Card from '../../components/card';
import Heading from '../../components/heading';
import Input from '../../components/input';
import styles from './Home.module.scss';
import Coconut from '../../assets/images/coconut.svg';
import BackTree from '../../components/backgroundTrees';
import Search from '../../assets/icons/Search.svg';
import Filter from '../../assets/icons/Filter.svg';
import Profile from '../../components/Profile';
import { Skeleton } from '@mui/material';
const HomeScreen: React.FC<{ loading: any; products: any }> = ({
  loading,
  products,
}) => {
  const [searchQuery, setsearchQuery] = useState('');
  return (
    <div>
      <Heading>Home</Heading>
      {/* <Profile /> */}
      <div className={styles.SearchCont}>
        <Input
          liveValue={(data: any) => setsearchQuery(data)}
          icon={Search}
          placeholder='Search'
        />
        {/* <img alt='' src={Filter} /> */}
      </div>
      <div className={styles.cardContainer}>
        {loading
          ? new Array(10)
              .fill(1)
              .map((val, idx) => (
                <Skeleton
                  key={'Skeleton_' + idx}
                  width={'10rem'}
                  height={'10rem'}
                />
              ))
          : products
              .filter((o: any) => o.name.includes(searchQuery))
              .map((o: any, index: Number) => (
                <Card
                  key={'Cards_' + index}
                  image={o.image}
                  heading={o.name}
                  price={o.price.price}
                  favourite={true}></Card>
              ))}
      </div>
      <BackTree></BackTree>
    </div>
  );
};

export default HomeScreen;
