import { takeLatest, call, put } from 'redux-saga/effects';
import * as FeedActions from '../actions/Feed.action';
import { Feeds } from '../../services';
import { keys } from '../keys';

const { GET_FEEDS, CREATE_FEEDS } = keys;

export function* fetchFeeds() {
  yield takeLatest(GET_FEEDS, getFeedDetails);
}

export function* getFeedDetails(action: any) {
  // @ts-ignore
  const data: any = yield call(Feeds.getFeeds);
  yield put(FeedActions.fetchFeedSuccess(data));
}

export function* createFeed() {
  yield takeLatest(CREATE_FEEDS, sendFeedDetails);
}

export function* sendFeedDetails(action: any) {
  // @ts-ignore
  const data: any = yield call(Feeds.createFeed, action.payload);
}
