import React, { useState } from 'react';
import Card from '../../components/card';
import Heading from '../../components/heading';
import Input from '../../components/input';
import styles from './Feeds.module.scss';
import Coconut from '../../assets/images/coconut.svg';
import BackTree from '../../components/backgroundTrees';
import Search from '../../assets/icons/Search.svg';
import Profile from '../../components/Profile';
import NewsCard from '../../components/NewsCard';
import { Skeleton } from '@mui/material';

const FeedsScreen: React.FC<{ loading: any; feeds: any }> = ({
  loading,
  feeds,
}) => {
  const [searchQuery, setsearchQuery] = useState('');
  return (
    <div>
      <Heading>Feeds</Heading>
      {/* <Profile /> */}
      <div className={styles.SearchCont}>
        <Input
          liveValue={(data: any) => setsearchQuery(data)}
          icon={Search}
          placeholder='Search'
        />
        {/* <img alt='' src={Filter} /> */}
      </div>
      <div className={styles.cardContainer}>
        {loading
          ? new Array(10)
              .fill(1)
              .map((val, idx) => (
                <Skeleton
                  key={'Skeleton_' + idx}
                  width={'-webkit-fill-available'}
                  height={'15rem'}
                />
              ))
          : feeds
              .filter((o: any) => o.content.includes(searchQuery))
              .map((o: any, index: number) => (
                <NewsCard
                  image={o.image}
                  content={o.content}
                  date={new Date(o.createdAt).toLocaleDateString('en-US', {
                    weekday: 'long',
                    year: 'numeric',
                    month: 'long',
                    day: 'numeric',
                  })}></NewsCard>
              ))}
      </div>
      <BackTree></BackTree>
    </div>
  );
};

export default FeedsScreen;
